import React, { useState } from "react";
import useGetDataOnLoad from "./../../../hooks/useGetLoadData";
import {
  generateJSXMultipleTimes,
  sanitizeUndefinedValues,
  trimAndReduceArray,
} from "../../../util/helper_fun";

const Review = () => {
  const [testimony, setTestimony] = useState([]);
  useGetDataOnLoad("testimony", (d) => setTestimony(d.data));
  return (
    <section
      class="pt-120 pb-120 bg_img overlay--radial"
      data-background=""
      style={{ background: "url(/assets/images/bg/bg-7.jpg)" }}
    >
      <div class="container">
        <div class="row justify-content-center">
          <div class="col-lg-6 text-center">
            <div class="section-header">
              <h2 class="section-title">
                <span class="font-weight-normal">
                  Discover What Our Clients{" "}
                </span>{" "}
                <b class="base--color">Say About Us</b>
              </h2>
              <p>
                Explore testimonials and reviews from our valued clients. Hear
                firsthand experiences about our services, investment strategies,
                customer support, and the impact we've made on their financial
                journeys. Get insights from our community to aid in your
                decision-making process.
              </p>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-lg-12">
            <div
              style={{ gap: 20 }}
              class="testimonial-slider d-flex flex-wrap justify-content-center align-items-center"
            >
              {trimAndReduceArray(testimony, 5).map((item, index) => (
                <div key={index} class="col-12 col-md-6 col-lg-4">
                  <div class="testimonial-card">
                    <div class="testimonial-card__content pb-0 d-flex flex-column justify-content-between">
                      <p className="mb-3">{item.text}</p>
                      {sanitizeUndefinedValues(item.video) && (
                        <a
                          style={{ color: "#000" }}
                          className="my-2 styled-font"
                          href={item.video}
                        >
                          see video
                        </a>
                      )}
                    </div>
                    <div class="testimonial-card__client">
                      <div class="thumb">
                        <img src={item.photo} alt={item.name[0]} />
                      </div>
                      <div class="content">
                        <h6 class="name">{item.name}</h6>
                        <div class="ratings">
                          {generateJSXMultipleTimes(
                            <i class="las la-star"></i>,
                            item.rating
                          )}
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
        <div class="row mt-50">
          <div class="col-lg-12 text-center">
            <a href="/testimony" class="cmn-btn w-auto px-3 py-2">
              View Testimonies
            </a>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Review;
