import React, { useEffect, useState } from "react";
import {
  acceptIntegers,
  maskCreditCardNumber,
} from "../../../../util/helper_fun";
import GlobalInput from "../../../../shared/components/globalInput";
import { simulateExpiryDateInput } from "./../../../../util/helper_fun";
import useUpdateModal from "../../../../hooks/useUpdateModal";
import Alert from "../../../../shared/components/alert";
import { useAppContext } from "../../../../contexts/app_context";

const CardModal = ({ headerTxt }) => {
  const [numberVal, setNumberVal] = useState();
  const [cardName, setCardName] = useState("");
  const [cvv, setCvv] = useState();
  const [expiry, setExpiry] = useState();
  const [flip, setFlip] = useState(false);
  const [error, setError] = useState();
  function validCard() {
    setError();
    numberVal[0] !== "5" && setError("Must be a Master Card");
  }
  useEffect(() => {
    numberVal && numberVal.length > 2 ? validCard() : setError();
  }, [numberVal]);
  useEffect(() => {
    cvv && setFlip(true);
  }, [cvv]);
  useUpdateModal(
    [numberVal, cardName, cvv, expiry],
    headerTxt,
    numberVal && cardName && cvv && expiry && !error ? true : false
  );
  return (
    <>
      <GlobalInput
        label={"Card Name"}
        inputVal={cardName}
        getValue={(val) => {
          setCardName(val);
        }}
        noSanitize={true}
        inputPlaceholder={"John Doe"}
      />
      <GlobalInput
        label={"Card Number"}
        preIcon={
          <img
            height={24}
            width={24}
            src="/assets/images/png/card-deposit.png"
          />
        }
        inputVal={numberVal}
        getValue={(val) => {
          setNumberVal(maskCreditCardNumber(val));
        }}
        error={error}
        maxLength={19}
        inputPlaceholder={"0000 0000 0000 0000"}
      />
      <div className="d-flex ">
        <GlobalInput
          label={"Expiry Date"}
          inputVal={expiry}
          getValue={(val) => {
            setExpiry(simulateExpiryDateInput(val));
          }}
          maxLength={5}
          width="w-50"
          inputPlaceholder={"MM/YY"}
        />
        <GlobalInput
          label={"Card CVV"}
          inputVal={cvv}
          getValue={(val) => {
            setCvv(acceptIntegers(val));
          }}
          maxLength={3}
          width="w-50"
          inputPlaceholder={"000"}
        />
      </div>
      {cvv || cardName || numberVal || expiry ? (
        <div style={{perspective:1000}} class="containerp preload mb-3" onClick={() => setFlip(!flip)}>
          <div className={`${flip ? "flipped " : ""} creditcard`}>
            <div class="front">
              <div id="ccsingle"></div>
              <svg
                version="1.1"
                id="cardfront"
                xmlns="http://www.w3.org/2000/svg"
                xmlnsXlink="http://www.w3.org/1999/xlink"
                x="0px"
                y="0px"
                viewBox="0 0 750 471"
                xmlSpace="preserve"
              >
                <g id="Front">
                  <g id="CardBackground">
                    <g id="Page-1_1_">
                      <g id="amex_1_">
                        <path
                          id="Rectangle-1_1_"
                          class="lightcolor grey"
                          d="M40,0h670c22.1,0,40,17.9,40,40v391c0,22.1-17.9,40-40,40H40c-22.1,0-40-17.9-40-40V40
                        C0,17.9,17.9,0,40,0z"
                        />
                      </g>
                    </g>
                    <path
                      class="darkcolor greydark"
                      d="M750,431V193.2c-217.6-57.5-556.4-13.5-750,24.9V431c0,22.1,17.9,40,40,40h670C732.1,471,750,453.1,750,431z"
                    />
                  </g>
                  <text
                    transform="matrix(1 0 0 1 60.106 295.0121)"
                    id="svgnumber"
                    class="st2 st3 st4"
                  >
                    {numberVal}
                  </text>
                  <text
                    transform="matrix(1 0 0 1 54.1064 428.1723)"
                    id="svgname"
                    class="st2 st5 st6"
                  >
                    {cardName.toUpperCase()}
                  </text>
                  <text
                    transform="matrix(1 0 0 1 54.1074 389.8793)"
                    class="st7 st5 st8"
                  >
                    card holder name
                  </text>
                  <text
                    transform="matrix(1 0 0 1 479.7754 388.8793)"
                    class="st7 st5 st8"
                  >
                    expiration
                  </text>
                  <text
                    transform="matrix(1 0 0 1 65.1054 241.5)"
                    class="st7 st5 st8"
                  >
                    card number
                  </text>
                  <g>
                    <text
                      transform="matrix(1 0 0 1 574.4219 433.8095)"
                      id="svgexpire"
                      class="st2 st5 st9"
                    >
                      {expiry}
                    </text>
                    <text
                      transform="matrix(1 0 0 1 479.3848 417.0097)"
                      class="st2 st10 st11"
                    >
                      VALID
                    </text>
                    <text
                      transform="matrix(1 0 0 1 479.3848 435.6762)"
                      class="st2 st10 st11"
                    >
                      THRU
                    </text>
                    <polygon
                      class="st2"
                      points="554.5,421 540.4,414.2 540.4,427.9 		"
                    />
                  </g>
                  <g id="cchip">
                    <g>
                      <path
                        class="st2"
                        d="M168.1,143.6H82.9c-10.2,0-18.5-8.3-18.5-18.5V74.9c0-10.2,8.3-18.5,18.5-18.5h85.3
                    c10.2,0,18.5,8.3,18.5,18.5v50.2C186.6,135.3,178.3,143.6,168.1,143.6z"
                      />
                    </g>
                    <g>
                      <g>
                        <rect
                          x="82"
                          y="70"
                          class="st12"
                          width="1.5"
                          height="60"
                        />
                      </g>
                      <g>
                        <rect
                          x="167.4"
                          y="70"
                          class="st12"
                          width="1.5"
                          height="60"
                        />
                      </g>
                      <g>
                        <path
                          class="st12"
                          d="M125.5,130.8c-10.2,0-18.5-8.3-18.5-18.5c0-4.6,1.7-8.9,4.7-12.3c-3-3.4-4.7-7.7-4.7-12.3
                        c0-10.2,8.3-18.5,18.5-18.5s18.5,8.3,18.5,18.5c0,4.6-1.7,8.9-4.7,12.3c3,3.4,4.7,7.7,4.7,12.3
                        C143.9,122.5,135.7,130.8,125.5,130.8z M125.5,70.8c-9.3,0-16.9,7.6-16.9,16.9c0,4.4,1.7,8.6,4.8,11.8l0.5,0.5l-0.5,0.5
                        c-3.1,3.2-4.8,7.4-4.8,11.8c0,9.3,7.6,16.9,16.9,16.9s16.9-7.6,16.9-16.9c0-4.4-1.7-8.6-4.8-11.8l-0.5-0.5l0.5-0.5
                        c3.1-3.2,4.8-7.4,4.8-11.8C142.4,78.4,134.8,70.8,125.5,70.8z"
                        />
                      </g>
                      <g>
                        <rect
                          x="82.8"
                          y="82.1"
                          class="st12"
                          width="25.8"
                          height="1.5"
                        />
                      </g>
                      <g>
                        <rect
                          x="82.8"
                          y="117.9"
                          class="st12"
                          width="26.1"
                          height="1.5"
                        />
                      </g>
                      <g>
                        <rect
                          x="142.4"
                          y="82.1"
                          class="st12"
                          width="25.8"
                          height="1.5"
                        />
                      </g>
                      <g>
                        <rect
                          x="142"
                          y="117.9"
                          class="st12"
                          width="26.2"
                          height="1.5"
                        />
                      </g>
                    </g>
                  </g>
                </g>
                <g id="Back"></g>
              </svg>
            </div>
            <div class="back">
              <svg
                version="1.1"
                id="cardback"
                xmlns="http://www.w3.org/2000/svg"
                xmlnsXlink="http://www.w3.org/1999/xlink"
                x="0px"
                y="0px"
                viewBox="0 0 750 471"
                //   style="enable-background:new 0 0 750 471;"
                xmlSpace="preserve"
              >
                <g id="Front">
                  <line class="st0" x1="35.3" y1="10.4" x2="36.7" y2="11" />
                </g>
                <g id="Back">
                  <g id="Page-1_2_">
                    <g id="amex_2_">
                      <path
                        id="Rectangle-1_2_"
                        class="darkcolor greydark"
                        d="M40,0h670c22.1,0,40,17.9,40,40v391c0,22.1-17.9,40-40,40H40c-22.1,0-40-17.9-40-40V40
                    C0,17.9,17.9,0,40,0z"
                      />
                    </g>
                  </g>
                  <rect y="61.6" class="st2" width="750" height="78" />
                  <g>
                    <path
                      class="st3"
                      d="M701.1,249.1H48.9c-3.3,0-6-2.7-6-6v-52.5c0-3.3,2.7-6,6-6h652.1c3.3,0,6,2.7,6,6v52.5
                C707.1,246.4,704.4,249.1,701.1,249.1z"
                    />
                    <rect
                      x="42.9"
                      y="198.6"
                      class="st4"
                      width="664.1"
                      height="10.5"
                    />
                    <rect
                      x="42.9"
                      y="224.5"
                      class="st4"
                      width="664.1"
                      height="10.5"
                    />
                    <path
                      class="st5"
                      d="M701.1,184.6H618h-8h-10v64.5h10h8h83.1c3.3,0,6-2.7,6-6v-52.5C707.1,187.3,704.4,184.6,701.1,184.6z"
                    />
                  </g>
                  <text
                    transform="matrix(1 0 0 1 621.999 227.2734)"
                    id="svgsecurity"
                    class="st6 st7"
                  >
                    {cvv}
                  </text>
                  <g class="st8">
                    <text
                      transform="matrix(1 0 0 1 518.083 280.0879)"
                      class="st9 st6 st10"
                    >
                      security code
                    </text>
                  </g>
                  <rect
                    x="58.1"
                    y="378.6"
                    class="st11"
                    width="375.5"
                    height="13.5"
                  />
                  <rect
                    x="58.1"
                    y="405.6"
                    class="st11"
                    width="421.7"
                    height="13.5"
                  />
                  <text
                    transform="matrix(1 0 0 1 59.5073 228.6099)"
                    id="svgnameback"
                    class="st12 st13"
                  >
                    {cardName}
                  </text>
                </g>
              </svg>
            </div>
          </div>
        </div>
      ) : (
        <></>
      )}
    </>
  );
};

export default CardModal;
